<template>
  <div class="landing-page">
    <img class="logo-image" src="../assets/logo.png">
    <h1>{{ msg }}</h1>
    <h2>{{ sorryMsg }}</h2>
  </div>
</template>

<script>
export default {
  name: 'landing-page',
  data () {
    return {
      msg: 'Welcome to Ovarlay App site.',
      sorryMsg: 'We are working hard to present the site as soon as possible.'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.landing-page {
  margin-top: 5vh;
  text-align: center;
}
.logo-image {
  width: 50vmin;
  height: 50vmin;
}
</style>



